import AnimatedCard from "../AnimatedCard/AnimatedCard";
import CardTopMediaContainer from "../../../../components/HallCard/CardTopMediaContainer/CardTopMediaContainer";
import CardTopInfoBlock from "../../../../components/HallCard/CardTopInfoBlock/CardTopInfoBlock";
import styles from "./styles.module.scss";
import { useGetHallByIdQuery } from "../../../../api/hallsAPI";
import { FC } from "react";
import { HallOrderProps } from "./interface";
import CounterEditor from "../../../../components/CounterEditor/CounterEditor";
import HallCardName from "../../../../components/HallCard/CardName/HallCardName";
import SkeletonCard from "../../../../ui/SkeletonCard/SkeletonCard";
import {
  useGetUserByTgIDQuery,
  userApiSlice,
  useUpdateCartMutation,
} from "../../../../api/userAPI";
import { TG_ID } from "../../../../shared/constants/AppOptions";
import {
  calcPrice,
  getHourForm,
} from "../../../../shared/functions/hallHelpers";
import { useRef } from "react";
import { useAppDispatch } from "../../../../shared/hooks/reduxTypes";
const HallOrder: FC<HallOrderProps> = ({
  data,
  isLoading,
  plusHourClick,
  minusHourClick,
}) => {
  const { data: userData } = useGetUserByTgIDQuery(Number(TG_ID));
  const typingTimerRef = useRef<NodeJS.Timeout | null>(null); // ref для таймера
  const [updateCart] = useUpdateCartMutation();
  const dispatch = useAppDispatch();

  const updateCartWithDelay = (newHour: number) => {
    if (typingTimerRef.current) {
      clearTimeout(typingTimerRef.current);
    }
    typingTimerRef.current = setTimeout(() => {
      if (userData?.id) {
        updateCart({ booking_time: newHour, user_id: userData.id });
      }
    }, 1000);
  };

  const minusBtnClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (
      userData &&
      userData.booking_time !== null &&
      userData.booking_time > 1
    ) {
      const update = userApiSlice.util.updateQueryData(
        "getUserByTgID",
        Number(TG_ID),
        (draft) => {
          if (draft?.booking_time) {
            draft.booking_time -= 1;
          }

          return draft;
        }
      );

      dispatch(update);
      updateCartWithDelay(userData.booking_time - 1); // Передаем новое значение

      if (minusHourClick) {
        minusHourClick();
      }
    }
  };

  const plusBtnClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (
      userData &&
      userData.booking_time !== null &&
      userData.booking_time < 72
    ) {
      const update = userApiSlice.util.updateQueryData(
        "getUserByTgID",
        Number(TG_ID),
        (draft) => {
          if (draft?.booking_time) {
            draft.booking_time += 1;
          }

          return draft;
        }
      );

      dispatch(update);
      updateCartWithDelay(userData.booking_time + 1); // Передаем новое значение

      if (plusHourClick) {
        plusHourClick();
      }
    }
  };

  
  if (isLoading) {
    return <SkeletonCard className={styles.skelet} />;
  }

  if (data) {
    if (Object.entries(data).length === 0) {
      return (
        <div
          style={{
            textAlign: "center",
            color: "var(--main-text-color)",
            marginTop: 30,
          }}
        >
          Ничего не найдено!
        </div>
      );
    }
    return (
      <>
        {Object.entries(data)[0][1] && (
          <AnimatedCard
            className={styles.card}
            mediaChild={
              <>
                <CardTopMediaContainer>
                  <CardTopInfoBlock>
                    {Object.entries(data)[0][1].max_people} чел.
                  </CardTopInfoBlock>
                </CardTopMediaContainer>

                <HallCardName name={Object.entries(data)[0][1].name} />
              </>
            }
            image={
              Object.entries(data)[0][1].photo &&
              Object.entries(data)[0][1].photo[0]
                ? Object.entries(data)[0][1].photo[0]
                : ""
            }
          >
            <CounterEditor
              plusBtnClick={plusBtnClick}
              minusBtnClick={minusBtnClick}
              className={styles.counter}
              counterBlockChildren={
                <>
                  {" "}
                  {userData?.booking_time}{" "}
                  {getHourForm(
                    userData?.booking_time ? userData?.booking_time : 0
                  )}{" "}
                  -{" "}
                  {calcPrice(
                    userData?.booking_time ? userData?.booking_time : 0,
                    Object.entries(data)[0][1].price_per_hour
                  )}{" "}
                  руб
                </>
              }
            ></CounterEditor>
          </AnimatedCard>
        )}
      </>
    );
  }

  return <></>;
};

export default HallOrder;
