import React from "react";
import ReactDOM from "react-dom/client";
import "./App/index.scss";
import App from "./App/App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter } from "react-router-dom";
import { ThemeProvider } from "@mui/material";
import { UserThemeProvider } from "./shared/hooks/useTheme";
import theme from "./shared/theme/MaterialUITheme";
import { TelegramProvider } from "./shared/hooks/useTelegram";
import { AnimationProvider } from "./shared/hooks/usePageAnimationClassName";
import { Provider } from "react-redux";
import { store } from "./App/store";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <Provider store={store}>
      <TelegramProvider>
        <UserThemeProvider>
          <ThemeProvider theme={theme}>
            <AnimationProvider>
              <BrowserRouter>
                <App />
              </BrowserRouter>
            </AnimationProvider>
          </ThemeProvider>
        </UserThemeProvider>
      </TelegramProvider>
    </Provider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
