import React, {
  createContext,
  Dispatch,
  useContext,
  useEffect,
  useMemo,
  useState,
} from "react";
import { IWebApp } from "../types/types";

export interface ITelegramContext {
  webApp: IWebApp;
}

const TelegramContext = createContext<ITelegramContext | undefined>(undefined);

export const TelegramProvider = ({
  children,
}: {
  children: React.ReactNode;
}) => {
  const [webApp, setWebApp] = useState<IWebApp | undefined>(undefined);

  useEffect(() => {
    const app = (window as any).Telegram?.WebApp;
    if (app) {
      app.ready();
      app.expand();
      app.disableVerticalSwipes();
      setWebApp(app);
    }
  }, []);

  const value = useMemo(() => {
    if (webApp) {
      return {
        webApp,
      };
    }
    return undefined;
  }, [webApp]);


  if (!value) {
    return null; // Возвращаем null, пока webApp и user не будут установлены
  }

  return (
    <TelegramContext.Provider value={value}>
      {children}
    </TelegramContext.Provider>
  );
};

export const useTelegram = () => {
  const context = useContext(TelegramContext);
  if (context === undefined) {
    throw new Error("useTelegram must be used within a TelegramProvider");
  }
  return context;
};
