import { FoodListItemApiModel } from "../../../../api/models/foodListApiModel";
import { BaseMenuListInterface } from "../../models/BaseListInterface";
import { FC, useEffect } from "react";
import MenuList from "../MenuList/MenuList";
import SkeletonCard from "../../../../ui/SkeletonCard/SkeletonCard";
import AnimatedCard from "../AnimatedCard/AnimatedCard";
import StyledMenuCard from "../../../../ui/MenuCard/MenuCard";
import {
  useGetUserByTgIDQuery,
  userApiSlice,
  useUpdateCartMutation,
} from "../../../../api/userAPI";
import { TG_ID } from "../../../../shared/constants/AppOptions";
import { useAppDispatch } from "../../../../shared/hooks/reduxTypes";
import CardTopInfoBlock from "../../../../components/HallCard/CardTopInfoBlock/CardTopInfoBlock";
import { useRef } from "react";

const FoodsList: FC<BaseMenuListInterface<FoodListItemApiModel[]>> = ({
  isError,
  isLoading,
  data,
}) => {
  const firstRender = useRef<boolean>(false);
  const { data: userData } = useGetUserByTgIDQuery(Number(TG_ID));
  const dispatch = useAppDispatch();



  if (isLoading) {
    return (
      <MenuList>
        <SkeletonCard />
        <SkeletonCard />
        <SkeletonCard />
        <SkeletonCard />
        <SkeletonCard />
        <SkeletonCard />
      </MenuList>
    );
  }
  return (
    <MenuList>
      {data &&
        data.map((el) => {
          return (
            <AnimatedCard key={el.id}>
              <StyledMenuCard
                image={el.photo}
                price={el.price}
                addInfo={` (${el.weight} г.)`}
                name={el.name}
                is_busket={userData?.data.food[String(el.id)] ? true : false}
                onClickPrice={() => {
                  if (
                    userData &&
                    userData?.data?.food &&
                    !userData?.data?.food[String(el.id)]
                  ) {
                    const update = userApiSlice.util.updateQueryData(
                      "getUserByTgID",
                      Number(TG_ID),
                      (draft) => {
                        const cartData = draft.data;

                        cartData.food[String(el.id)] = {
                          quantity: 1,
                          price: el.price,
                          weight: el.weight,
                          name: el.name,
                          photo: el.photo,
                        };

                        return draft;
                      }
                    );

                    dispatch(update);
                  }

                  const data = JSON.parse(JSON.stringify(userData));

                  if (data.data) {
                    data.data.food[String(el.id)] = {
                      quantity: 1,
                      price: el.price,
                      weight: el.weight,
                      name: el.name,
                    };
                  }
                }}
              />
            </AnimatedCard>
          );
        })}
    </MenuList>
  );
};

export default FoodsList;
